import React from 'react';

class MyCard extends React.Component {
  render() {
    const img = 'cpng/' + this.props.img + '.jpg';
    const name = this.props.name;

    const status = this.props.status;
    var color = 'btn-primary';
    var text = 'Select';
    if (status) {
      color = 'btn-outline-danger';
      text = 'Unselect';
    }

    return (
      <div className="col-sm-4 col-md-3 col-lg-2">
        <div className="card gallery-item" style={{ height: "92%" }}>
          <img className="card-img-top" src={img} alt={name} />
          <div className="card-body" style={{ display: "flex", flexDirection: "column" }}>
            <h5 className="card-title">{name}</h5>
            <p style={{ marginTop: 'auto' }}>{this.props.type}</p>
            <button style={{ margin: "auto auto 8px auto" }} onClick={this.props.handler} className={`btn ${color}`}>
              {' '}
              {text}{' '}
            </button>

            {/* add on click to this button  */}
          </div>
        </div>
      </div>
    );
  }
}

export default MyCard;
