import React, { Fragment } from 'react';
import firebase from 'firebase/app';
import 'firebase/firestore';

import ReactLoading from 'react-loading';

export default class PriorityResults extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      scorecard: [],
    };

    this.unsubscribe = firebase.firestore()
      .collection('unselectedTeams').onSnapshot((snap) => {

        let tempDict = {};

        snap.forEach((doc) => {
          tempDict[doc.data()['teamName']] = doc.data()['money'];
        });

        // Sorting by key and creating an array
        var items = Object.keys(tempDict).map(function (key) {
          return [key, tempDict[key]];
        });

        // Sort the array based on the second element
        items.sort(function (first, second) {
          return second[1] - first[1];
        });

        this.setState({
          scorecard: items,
        });
      });
  }

  componentWillUnmount() {
    this.unsubscribe();
  }

  render() {
    const scorecard = this.state.scorecard;
    let tableToCreate = [];
    var i = 0
    for (let key in scorecard) {
      i = i + 1

      let color='text-success';
      let diff = parseFloat(scorecard[key][1]).toFixed(2) - 3800000;
      if (diff < 0) {
        color = 'text-danger';
      } else if (diff === 0) {
        color = 'text-info';
      }

      tableToCreate.push(
        <Fragment key={key}>
          <tr className="table-info" style={{ textAlign: 'center'}}>
            <td>{i}</td>
            <td>{scorecard[key][0]}</td>
            <td>{parseFloat(scorecard[key][1]).toFixed(2)}</td>
            <td class={color}>{diff}</td>
          </tr>
        </Fragment>
      );
    }

    let elements = [];

    if (!tableToCreate.length) {
      elements = (
        <div style={{ marginTop: "32px" }}>
          <ReactLoading type={"bars"} style={{ margin: "auto", width: "100px" }} />
        </div>
      )
    } else {
      elements = (
        <table
          style={{
            margin: 'auto',
            width: '70%',
            border: '1px solid black',
            borderRadius: '5px!important',
          }}
        >
          <thead className="thead-dark">
            <tr style={{ textAlign: 'center' }}>
              <th>Rank</th>
              <th>Team Name</th>
              <th>Money</th>
              <th>Net Profit</th>
            </tr>
          </thead>
          <tbody>{tableToCreate}</tbody>
        </table>
      )
    }

    return (
      <div className="table" style={{ padding: '60px' }}>
        <h1 style={{ textAlign: 'center' }}>IPO Leaderboard</h1>

        {elements}
      </div>
    );
  }
}
