import React from 'react';
import playerdata from '../data/playerData.json';

export default function Stats(props) {
  //Props receive playerno props.playerNo
  var pdata = playerdata[props.playerNo];
  const heading = [];
  const data = {};
  try {
    for (const [year, value] of Object.entries(pdata)) {
      // use key and value
      heading.push(<td key={year} style={{ fontWeight: 'bold' }}>{year}</td>);
      for (const [field, value1] of Object.entries(value)) {
        if (data[field]) {
          data[field].push(value1);
        } else {
          data[field] = [value1];
        }
      }
    }
  } catch (error) {
    console.log('stats.js mein sed lyf 3');
  }

  const Body = [];
  for (var field in data) {
    const tempList = [];
    tempList.push(<td key={0} style={{ fontWeight: 'bold' }}>{field}</td>);

    let j = 1;
    for (var ob in data[field]) {
      tempList.push(<td key={j}>{data[field][ob]}</td>);
      j++;
    }
    Body.push(<tr key={field} >{tempList}</tr>);
  }

  // console.log('playerdata: ', data);

  return (
    <div
      id="player-stats"
      className="jumbotron"
      style={{ margin: '22px', padding: '10px' }}
    >
      <table className="table table-borderless">
        <thead>
          <tr>
            <td></td>

            {heading}
          </tr>
        </thead>

        <tbody>{Body}</tbody>
      </table>
    </div>
  );
}
