import React from 'react';

import firebase from 'firebase/app';
import 'firebase/firestore';

import { withSnackbar } from 'notistack';
import Button from '@material-ui/core/Button';
import ReactLoading from 'react-loading';

const themesArray = ['default', 'tblue', 'tred', 'tgreen', 'tyellow']

class Messages extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      messagesArray: [],
      cTheme: 0
    };


    this.unsubscribe = firebase
      .firestore()
      .collection('admin')
      .doc('updates')
      .onSnapshot(
        (snap) => {
          try {
            // console.log('messages', snap.data());
            this.updateMessages(snap.data()['announcements'], true);
          } catch (error) {
            this.updateMessages(['Fetch Failed'], false);
          }
        },
        (error) => {
          console.log('Messages Error came', error);
        },
      );
  }

  getCookie = (cname) => {
    var name = cname + "=";
    var decodedCookie = decodeURIComponent(document.cookie);
    var ca = decodedCookie.split(';');
    for (var i = 0; i < ca.length; i++) {
      var c = ca[i];
      while (c.charAt(0) === ' ') {
        c = c.substring(1);
      }
      if (c.indexOf(name) === 0) {
        return c.substring(name.length, c.length);
      }
    }
    return "";
  }

  setCookie = (cname, cvalue) => {
    document.cookie = cname + "=" + cvalue + ";path=/";
  }

  updateMessages = (messagesArray, success) => {

    const currentMessageArray = this.state.messagesArray;

    if ((messagesArray.length - currentMessageArray.length) < 3) {

      console.log("Show innovative update");

      let variant = 'success'
      if (!success) {
        variant = 'error';
      }

      this.props.enqueueSnackbar(messagesArray[messagesArray.length - 1], { variant: variant });
    } else {
      this.props.enqueueSnackbar('Welcome Guys ', { variant: "info" })
    }

    this.setState({
      messagesArray: messagesArray,
    });
  }

  themeToggler = () => {
    let cTheme = this.state.cTheme;

    document.getElementById('main-content').classList.remove(themesArray[cTheme]);
    cTheme += 1

    if (cTheme >= themesArray.length) {
      cTheme = 0;
    }

    document.getElementById('main-content').classList.add(themesArray[cTheme]);

    this.setState({
      cTheme: cTheme
    });

    this.setCookie('themeData', cTheme);

  }

  componentDidMount() {

    // For getting themes Cookies
    const cookie = this.getCookie('themeData');
    console.log("Cookie found", cookie);
    if (cookie) {

      document.getElementById('main-content').classList.add(themesArray[cookie]);

      this.setState({
        cTheme: parseInt(cookie)
      });
    }
  }

  componentWillUnmount() {
    this.unsubscribe();
  }

  render() {
    const messageEl = [];
    const messagesArray = this.state.messagesArray;
    for (let i = messagesArray.length - 1; i >= 0; i--) {
      if (i === messagesArray.length - 1) {
        messageEl.push(<li key={i} className="text-primary">{messagesArray[i]}</li>);
      } else {
        messageEl.push(<li key={i}>{messagesArray[i]}</li>);
      }
    }

    const elements = []

    if (!messageEl.length) {
      elements.push(
        <div key={0} style={{ overflow: 'auto', padding: 0, height: '60vh', color: "#7d7d7d" }}>
          <ReactLoading key={0} type={"bubbles"} style={{ margin: "auto", height: "80px", width: "80px" }} color="#000000" />
        </div>
      )
    } else {
      elements.push(
        <ul key={0} style={{ overflow: 'auto', height: '60vh', color: "#7d7d7d" }}>
          {messageEl}
        </ul>
      )
    }

    return (
      <div style={{ position: "sticky", top: 0 }}>
        <div className="jumbotron updates-table" id="updates-container" style={{ marginRight: "-15px" }}>
          <h1 className="center">Updates</h1>
          {elements}
        </div>

        <div>
          <Button onClick={this.themeToggler}>Toggle Theme</Button>
        </div>
      </div>
    );
  }
}

export default withSnackbar(Messages);
