import React, { Fragment } from 'react';

import firebase from 'firebase/app';
import 'firebase/firestore';

import LoginHandler from '../LoginHandler';
import {
  Dialog,
  DialogContent,
  DialogContentText,
  DialogActions,
  Button
} from '@material-ui/core';

class Prestart extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      open: false,
      message: "",
      enter: false
    }
  }

  handleDialogClose = () => {
    this.setState({
      message: "",
      open: false
    });
  }

  checkCode = (tCode) => {
    // console.log('Team code got', tCode);

    const db = firebase.firestore();
    db.collection('answers').doc(tCode).get().then(
      doc => {
        if (doc.exists) {
          // console.log("Success the code exists");
          this.setState({
            message: "The code is valid!",
            enter: true
          });

        } else {
          this.setState({
            open: true,
            message: "This code does not exist!"
          });
        }
      }
    ).catch(error => {
      console.log("error came", error);
      this.setState({
        open: true
      });
    });
  }

  render() {

    let introBtn = [
      <h4 className="center">Checkout the </h4>,
      <div style={{ marginTop: "32px", display: "flex" }}>
        <a href="/intro/" style={{ margin: "auto" }} className="btn btn-info">Intro Page</a>
      </div>

    ];

    if (this.state.enter) {

      return (
        <Fragment>

          <div className="jumbotron container" style={{ marginTop: "10vh" }}>

            <h2 className="center">Great ! Your secret Code is working well.</h2>
            <h4 className="center">Please wait till the quiz round starts. In the meantime, read the rules for the quiz round.</h4>
            <div style={{
              margin: "auto",
              padding: "10px"
            }}
            >
              <ul>
                <li><code>Total Questions :</code> 12</li>
                <li><code>Total time :</code> 30 minutes</li>
                <li><code>Total marks :</code> 50</li>
                <li>Different questions may have different marks associated with them.</li>
                <li>Some questions may have <code>partial marking</code></li>
                <li>Marking scheme of questions is NOT shared</li>
                <li>For questions with multiple answers, please write the answers in proper sequence</li>
                <li>Reminders will be sent after <code>15, 25 and 29 minutes</code>. Make sure that you have submitted all your answers by clicking the submit button. No requests to extra time after the 30 minutes will be entertained.</li>
                <li>Rules for Tiebreaker</li>
                <blockquote style={{ marginLeft: "22px" }}>

                  <li><strong>Backward marking</strong> - In case of equal scores, questions will be checked from the last. In first case of difference, the answer with lesser marks loses.</li>
                  <li><strong>Special Tiebreaker round</strong> - If a tie still persists, the teams will be invited to a separate google meet for a special tie breaker question.</li>

                </blockquote>
              </ul>

            </div>
          </div>
          {introBtn}
        </Fragment >
      )
    } else {

      return (
        <Fragment>
          <h1 className="center" style={{ margin: "32px" }}>The game will start soon!</h1>
          <LoginHandler submitHandler={this.checkCode} />

          {introBtn}

          {/* dialog for telling about */}
          <Dialog
            open={this.state.open}
            onClose={this.handleDialogClose}
            aria-labelledby="Login Failed"
            aria-describedby="Fail dialog"
          >
            <DialogContent>
              <DialogContentText id="alert-dialog-description">
                {this.state.message}
              </DialogContentText>
            </DialogContent>
            <DialogActions>
              <Button onClick={this.handleDialogClose} color="primary">
                close
              </Button>
            </DialogActions>
          </Dialog>
        </Fragment>
      )
    }
  }
}

export default Prestart;
