import React, { Fragment } from 'react';
import firebase from 'firebase/app';
import 'firebase/firestore';
import '../../css/teams.css';
import UnseletedTeams from '../unselectedTeams';
import TeamCard from '../teamCard.js';

class Teams extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      teamDetails: {},
      predictionDict: {},
      toShowSelected: true,
      unTeamDetails: {},
      priorityDict: {},
      sharesDict: {}
    };

    // Adding firebase listeners in constructor so that they can be unsubscribed later
    this.unsubscribe = firebase.firestore().collection('selectedTeams') //change this back to selected teams :p
      .onSnapshot(
        (snap) => {
          var Dict = {};
          snap.forEach((doc) => {
            const data = doc.data();
            Dict[doc.id] = data;
          });

          this.setState({
            teamDetails: Dict,
          });
        },
        (err) => {
          console.log('Fetch Failed');
        },
      );

    this.unsubscribeForUnselected = firebase.firestore()
      .collection('unselectedTeams') //change this back to selected teams :p
      .onSnapshot(
        (snap) => {
          var Dict = {};
          snap.forEach((doc) => {
            const data = doc.data();
            Dict[doc.id] = data;
          });

          this.setState({
            unTeamDetails: Dict,
          });
        },
        (err) => {
          console.log('Fetch Failed');
        },
      );
  } // End of constructor

  showSelected = () => {
    this.setState({
      toShowSelected: true,
    });
  };

  showUnselected = () => {
    this.setState({
      toShowSelected: false,
    });
  };

  componentDidMount() {

    // console.log(this.props.round);

    if (this.props.round !== 'prediction') {
      console.log('if is working');

      var predictionArray = {};

      firebase.firestore().collection('predictions')
        .get()
        .then((snap) => {
          snap.forEach((doc) => {
            predictionArray[doc.id] = doc.data();
          });

          this.setState({
            predictionDict: predictionArray,
          });
        });

      var priorityArray = {};

      firebase.firestore().collection('priority')
        .get()
        .then((snap) => {
          snap.forEach((doc) => {
            priorityArray[doc.id] = doc.data();
          });

          this.setState({
            priorityDict: priorityArray,
          });

        });

      let sharesDict = {};

      firebase.firestore().collection("priorityPlayers").get().then((snap) => {
        snap.forEach((doc) => {
          var playerNo = doc.id;
          var teamOwners = doc.data()["teams"];
          var percentShare = Math.floor(100 / teamOwners.length);
          for (var i = 0; i < teamOwners.length; i++) {
            if (!sharesDict[teamOwners[i]]) {
              sharesDict[teamOwners[i]] = {};
            }
            sharesDict[teamOwners[i]][playerNo] = percentShare;
          }
        })

        console.log("Shares Dict", sharesDict)
        this.setState({
          sharesDict: sharesDict,
        })
      });

    } else {
      console.log('prediction is going on');
    }
  }

  componentWillUnmount() {
    this.unsubscribe();
    this.unsubscribeForUnselected();
  }

  render() {

    if (this.state.toShowSelected) {
      const teamDetails = this.state.teamDetails;

      const teamCodes = [];
      const teamNames = [];
      const teamMembers = [];
      const teamLeaders = [];
      const allTeamsComponent = [];
      const predictionDict = this.state.predictionDict;

      for (let key in teamDetails) {
        teamNames.push(teamDetails[key]['teamName']);
        teamMembers.push(teamDetails[key]['teamMembers']);
        teamLeaders.push(teamDetails[key]['teamLeader']);
        teamCodes.push(teamDetails[key]['teamCode']);
      }

      for (let key in teamDetails) {
        allTeamsComponent.push(
          <TeamCard
            key={key}
            teamName={teamNames[key - 1]}
            teamMember={teamMembers[key - 1]}
            teamLeader={teamLeaders[key - 1]}
            teamCode={teamCodes[key - 1]}
            prediction_arr={predictionDict[teamCodes[key - 1]]}
          />,
        );
      }

      return (
        <Fragment>
          <div id="navbar" className="container-fluid">
            <div className="row">
              <div className="col center" onClick={this.showSelected}>
                {' '}
                Teams for Auction
              </div>
              <div className="col center text-info" onClick={this.showUnselected}>
                {' '}
                Teams for IPO{' '}
              </div>
            </div>
          </div>

          <div className="container-fluid">{allTeamsComponent}</div>
        </Fragment>
      );
    } else {
      return (
        <Fragment>
          <div id="navbar" className="container-fluid">
            <div className="row">
              <div className="col center text-info" onClick={this.showSelected}>
                {' '}
                Teams for Auction
              </div>
              <div className="col center" onClick={this.showUnselected}>
                {' '}
                Teams for IPO{' '}
              </div>
            </div>
          </div>

          <div className="container-fluid">
            <UnseletedTeams unSelectedTeams={this.state.unTeamDetails} priorityDict={this.state.priorityDict} sharesDict={this.state.sharesDict} />
          </div>
        </Fragment>
      );
    }
  }
}

export default Teams;
