import React, { Fragment } from 'react';
import './App.css';
import { BrowserRouter as Router, Link, Route, Switch } from 'react-router-dom';
import firebase from 'firebase/app';
import 'firebase/firestore';
import ReactLoading from 'react-loading';

import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';

import Home from './components/pages/home';
import Player from './components/pages/players';
import Teams from './components/pages/teams';
import Quiz from './components/quiz';
import Messages from './components/messages';
import QuizWaiting from './components/QuizWaiting';
import QuizResult from './components/pages/QuizResult';
import Prestart from './components/pages/prestart';

class App extends React.Component {
  constructor() {
    super();

    // setting the inital state of the App
    this.state = {
      tabValue: 0,
      round: 'prestart321',
      messages: [],
    };

    // Don't forget to bind the functions to classes
    this.handleChange = this.handleChange.bind(this);
  }

  /* ----- Add functions here ----- */
  updateRound(round, lastSold) {

    // round is integer or string or say between 1 to 60
    try {
      if (round <= 60 && round >= 1) {
        console.log("Round", round);
        console.log(lastSold);
      }
    } catch (error) {
      console.log("Not a number");
    }

    this.setState({
      round: round,
    });
  }

  handleChange(event, newValue) {
    this.setState({
      tabValue: newValue,
    });
    console.log('Handle Change', newValue);
  }

  /* ----- Add Firebase details here ------ */
  // Add firebase updates and listener here
  componentDidMount() {
    const db = firebase.firestore();
    const doc = db.collection('admin').doc('current');

    doc.onSnapshot(
      (snap) => {
        try {
          // console.log('Recieved Round Info', snap.data().pid);
             this.updateRound(snap.data().pid, snap.data().lastSold);
        } catch (error) {
          console.log('Main fetch failed', error);
        }
      },
      (err) => {
        console.log(`Encountered error: ${err}`);
      },
    );
  }

  render() {
    // First check for the Quiz Round from the firebase
    const r = this.state.round;

    let round = '...';
    if (r === 'quiz123') {
      round = 'quiz'
    } else if (r === 'wait546') {
      round = 'waiting'
    } else if (r === 'disp457') {
      round = 'display'
    } else if (r === 'prestart321') {
      round = 'prestart'
    } else if (r === 'prediction') {
      round = 'prediction'
    }
    else {
      // if ( (r !== 'quiz') && (r !== 'waiting') && (r !== 'prestart')) {
      //   round = r;
      // } else if {
        
      // }

      if (parseInt(r) >=1 && parseInt(r) <=60) {
        round = r;
      }
    }

    console.log("Round", round);
    console.log("R", r);

    // Check for the quiz round!
    if (round === '...') {
      return (
        <Fragment>
          <div style={{ display: "flex" }}>
            <div style={{ margin: "40vh auto", width: "100px" }}>
              <ReactLoading style={{ margin: "auto" }} type="spinningBubbles" color="#000000" height={'20%'} width={'20%'} />
            </div>
          </div>
        </Fragment>
      )
    } else if (round === 'prestart') {
      return (
        <div id="main-content" className="container-fluid">
          <Prestart />
        </div>
      )
    }
    else if (round === 'quiz') {
      return (
        <div id="main-content" className="container-fluid">
          <div className="row">
            <div className="col-sm-9">
              <Quiz />
            </div>
            <div className="col-sm-3">
              <Messages />
            </div>
          </div>
        </div>
      );
    } else if (round === 'waiting') {
      return (
        <Router>
          <AppBar position="static" color="transparent" id="nav-bar">
            <Tabs
              variant="fullWidth"
              aria-label="Navigation"
              value={this.state.tabValue}
              onChange={this.handleChange}
            >
              <Tab label="Answers" to="/" component={Link} />
              <Tab label="Players" to="/players" component={Link} />
            </Tabs>
          </AppBar>

          <div id="main-content">
            <Switch>
              <Route path="/players">
                <Player />
              </Route>

              <Route path="/">
                <div className="container-fluid">
                  <div className="row">
                    <div className="col-sm-9">
                      <QuizWaiting />
                    </div>
                    <div className="col-sm-3">
                      <Messages />
                    </div>
                  </div>
                </div>
              </Route>
            </Switch>
          </div>
        </Router>
      );
    } else if (round === 'display') {
      return (
        <Router>
          <AppBar position="static" color="transparent" id="nav-bar">
            <Tabs
              variant="fullWidth"
              aria-label="Navigation"
              value={this.state.tabValue}
              onChange={this.handleChange}
            >
              <Tab label="Players" to="/players" component={Link} />
              <Tab label="Teams" to="/teams" component={Link} />
              <Tab label="LeaderBoard" to="/leaderboard" component={Link}></Tab>
            </Tabs>
          </AppBar>

          <div id="main-content">
            <Switch>
              <Route path="/teams">
                <Teams />
              </Route>
              <Route path="/leaderboard">
                <QuizResult />
              </Route>

              <Route path="/players">
                <div className="container-fluid">
                  <div className="row">
                    <div className="col-sm-9">
                      <Player />
                    </div>
                    <div className="col-sm-3">
                      <Messages />
                    </div>
                  </div>
                </div>
              </Route>
            </Switch>
          </div>
        </Router>
      );
    } else {
      // The main content and routes everything
      return (
        <Router>
          <AppBar position="static" color="transparent" id="nav-bar">
            <Tabs
              variant="fullWidth"
              aria-label="Navigation"
              value={this.state.tabValue}
              onChange={this.handleChange}
            >
              <Tab label="Home" to="/" component={Link} />
              <Tab label="Players" to="/players" component={Link} />
              <Tab label="Teams" to="/teams" component={Link} />
              <Tab label="LeaderBoard" to="/leaderboard" component={Link}></Tab>
            </Tabs>
          </AppBar>

          <div id="main-content">
            <Switch>
              <Route path="/players">
                <Player />
              </Route>
              <Route path="/teams">
                <Teams round={this.state.round} />
              </Route>
              <Route path="/leaderboard">
                <QuizResult />
              </Route>
              <Route path="/">
                <div className="container-fluid">
                  <div className="row">
                    <div className="col-sm-9">
                      <Home round={round} />
                    </div>
                    <div className="col-sm-3">
                      <Messages />
                    </div>
                  </div>
                </div>
              </Route>
            </Switch>
          </div>
        </Router>
      );
    }
  } // End of render
}

export default App;
